/**
 * http://commonmark.org/help/
 */
var LI_HEADER = 'H2M_LI_HEADER';
module.exports = {
  em: function em(node) {
    if (node.md) {
      return "*".concat(node.md, "*");
    }
  },
  strong: function strong(node) {
    if (node.md) {
      return "**".concat(node.md, "**");
    }
  },
  h1: function h1(node) {
    if (node.md) {
      return "\n# ".concat(node.md, "\n");
    }
  },
  h2: function h2(node) {
    if (node.md) {
      return "\n## ".concat(node.md, "\n");
    }
  },
  h3: function h3(node) {
    if (node.md) {
      return "\n### ".concat(node.md, "\n");
    }
  },
  h4: function h4(node) {
    if (node.md) {
      return "\n#### ".concat(node.md, "\n");
    }
  },
  h5: function h5(node) {
    if (node.md) {
      return "\n##### ".concat(node.md, "\n");
    }
  },
  h6: function h6(node) {
    if (node.md) {
      return "\n###### ".concat(node.md, "\n");
    }
  },
  a: function a(node) {
    var text = node.md || node.attrs.href;
    var href = node.attrs.href || text;

    if (text) {
      return "[".concat(text, "](").concat(href, ")");
    }
  },
  img: function img(node) {
    var src = node.attrs.src;

    if (src) {
      return "![".concat((node.attrs.title || node.attrs.alt || '').trim(), "](").concat(src, ")");
    }
  },
  blockquote: function blockquote(node) {
    var md = node.md;

    if (md) {
      md = md.replace(/(^\n+|\n+$)/g, '');
      md = md.split('\n').map(function (line) {
        return "> ".concat(line, "\n");
      }).join('');
      return "\n".concat(md, "\n");
    }
  },
  ul: function ul(node) {
    if (node.md) {
      return "\n".concat(node.md.replace(new RegExp(LI_HEADER, 'ig'), '-'), "\n");
    }
  },
  ol: function ol(node) {
    var index = 1;

    if (node.md) {
      return "\n".concat(node.md.replace(new RegExp(LI_HEADER, 'ig'), function () {
        return "".concat(index++, ".");
      }), "\n");
    }
  },
  li: function li(node) {
    if (node.md) {
      return "".concat(LI_HEADER, " ").concat(node.md, "\n");
    }
  },
  hr: function hr() {
    return '\n---\n';
  },
  code: function code(node) {
    if (node.md) {
      if (node.isInPreNode) {
        return node.md;
      }

      return "`".concat(node.md, "`");
    }
  },
  br: function br() {
    return '\n';
  },
  pre: function pre(node) {
    var md = node.md;

    if (md) {
      md = md.split('\n').map(function (line) {
        return "    ".concat(line, "\n");
      }).join('');
      return "\n".concat(md, "\n");
    }
  },
  p: function p(node) {
    var md = node.md;

    if (md) {
      return "\n".concat(md, "\n");
    }
  },
  div: function div(node) {
    var md = node.md;

    if (md) {
      return "\n".concat(md, "\n");
    }
  },
  'default': function _default(node) {
    return node.md;
  },
  cleanup: function cleanup(result) {
    // remove leading or tailing break
    // convert \n\n\n... to \n\n
    return result.replace(/(^\n+|\n+$)/g, '').replace(/\n{3,}/g, '\n\n');
  }
};